import React from 'react';
import { Router, Route, Switch } from 'react-router-dom';
import { createBrowserHistory } from 'history';
import AddEventPage from '../components/AddEventPage';
import EditEventPage from '../components/EditEventPage';
import CalendarDashboardPage from '../components/CalendarDashboardPage';
import LoginPage from '../components/LoginPage';
import NotFoundPage from '../components/NotFoundPage';
import PrivateRoute from './PrivateRoute';
import PublicRoute from './PublicRoute';

export let history = createBrowserHistory();
history.push('/dashboard')
const AppRouter = () => (
    <Router history={history}>
        {console.log(history)}
        <div>
            <Switch>
                <Route path="/" component={LoginPage} exact={true} />
                <Route path="/dashboard" component={CalendarDashboardPage} />
                <Route path="/create" component={AddEventPage} />
                <Route path="/edit/:id" component={EditEventPage} />
                <Route component={NotFoundPage} />
            </Switch>
        </div>
    </Router>
);

export default AppRouter;